import * as React from 'react';
import { QaNav } from 'oned-react';
import { Icon } from '../../Icon';

class QaNavDemo extends React.Component<any> {
  handleClickAction = (message: string) => () =>
    new Promise((resolve, reject) => {
      setTimeout(() => {
        resolve(message);
      }, 1000);
    });

  render() {
    return (
      <QaNav
        origin="http://quickci.aliyun.test"
        navList={[]}
        userInfo={{
          accountName: 'Quick Audience',
          nickName: '管理员',
          isAdmin: true,
          roleName: 'Administrator',
          roleCode: 'ROLE_ES_ADMIN',
        }}
        helpCenterList={[
          {
            icon: <Icon type="oned-icon icon-CombinedShape" />,
            text: '帮助文档',
          },
        ]}
        instance={{
          versionName: '标准版',
          remainDay: 30,
        }}
        approvalData={{
          data: [
            {
              admins: ['管理员'],
              applicantName: '测试帐号1',
              applyContent: '',
              applyId: 'a',
              approverName: null,
              flagStatus: 0,
              gmtCreate: 1574845036000,
              gmtModified: 1574845036000,
              resourceType: 'bindingDataBank',
            },
          ],
          loading: false,
        }}
        applicationData={{
          loading: false,
          data: [
            {
              admins: ['管理员'],
              applicantName: '测试帐号1',
              applyContent: '',
              applyId: 'b',
              approverName: null,
              flagStatus: 0,
              gmtCreate: 1574845036000,
              gmtModified: 1574845036000,
              resourceType: 'bindingDataBank',
            },
          ],
        }}
        onCancelApproval={this.handleClickAction('撤销')}
        onRefuseApproval={this.handleClickAction('拒绝')}
        onAgreeApproval={this.handleClickAction('同意')}
      />
    );
  }
}

export default <QaNavDemo />;

